
import axios from 'axios'
import config from './../../public/config.json';

let HttpClient = axios.create({
    baseURL: `http://${config.addr}:${config.port}`
})

export default class Server {

    fetchDocs() {
        return HttpClient({
            url: '/doc/v1',
            method: 'get',
            headers: {
                'Content-Type': 'application/x-www-form-urlencode'
            }
        })
    }

    fetchUpdated(){
        return HttpClient({
            url: '/doc/v2/updated',
            method: 'get',
            headers: {
                'Content-Type': 'application/x-www-form-urlencode'
            }
        })
    }
}