<template >
  <div
    id="index"
    ref='card'
  >
    <div class="wrapper">
      <div class="index-container">

        <el-card
          class="box-card"
          :id="data['Title']"
        >
          <!-- header -->
          <div
            slot="header"
            class="clearfix"
            style="font-size:20px"
          >
            <span><strong>{{data['Title']}}</strong></span>
          </div>

          <div class="i-con">
            <p class="i-item">
              <span class="i-item-1">
                <h3>Url<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{data['Url']}}</span>
            </p>

            <p class="i-item">
              <span class="i-item-1">
                <h3>Header<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{data['Header']}}</span>
            </p>

            <p class="i-item">
              <span class="i-item-1">
                <h3>Method<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{data['Method']}}</span>
            </p>

            <!-- 请求参数 -->
            <el-table
              :data="data['Params']"
              style="width: 100%; font-size:18px"
            >
              <el-table-column
                prop="name"
                label="name"
              >
              </el-table-column>
              <el-table-column
                prop="type"
                label="type"
              >
              </el-table-column>
              <el-table-column
                prop="explain"
                label="explain"
              >
              </el-table-column>
              <el-table-column
                test-container
                prop="remark"
                label="remark"
              >
              </el-table-column>
              <el-table-column
                prop="other"
                label="other"
              >
              </el-table-column>
            </el-table>
            <div
              class="divider"
              @click="showTestAreaState=!showTestAreaState"
              :style="{'background-color':showTestAreaState?'#90929833':''}"
            >
              <i class="el-icon-d-arrow-right"></i>
            </div>
            <!-- 测试区 -->
            <div
              class="test-container"
              :style="{'height':showTestAreaState?this.$refs.testArea.scrollHeight+'px':''}"
            >
              <!-- 参数输入框 -->
              <div
                class="test-area"
                ref="testArea"
              >
                <div
                  class="t-area-item"
                  v-for="(p,index) in data['Params']"
                  :key="index"
                >
                  <label>
                    <Input
                      :label="p['name']"
                      v-model="p['value']"
                      v-if="p['type'] === 'string'"
                    />
                  </label>

                  <label>
                    <Input
                      :label="p['name']"
                      v-model="p['value']"
                      v-if="p['type'] === 'bool'"
                    />
                  </label>

                  <label>
                    <Input
                      :label="p['name']"
                      v-model="p['value']"
                      v-if="p['type'] === 'int'"
                    />
                  </label>

                  <label>
                    <Input
                      :label="p['name']"
                      v-model="p['value']"
                      v-if="p['type'] === 'float'"
                    />
                  </label>

                  <input
                    type="file"
                    v-if="p['type'] === 'file'"
                    @change="handleFileChange"
                    ref="uploadFile"
                  >

                </div>
                <button
                  class="t-btn"
                  @click="startTest()"
                >测试</button>
              </div>
              <!-- 测试结果区 -->
              <pre class="t-res-area">{{data['Result']}}</pre>
            </div>

          </div>

        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import Input from "./Input";
export default {
  name: "index",
  components: {
    Input
  },
  props: {
    data: Object
  },
  data() {
    return {
      file: "",
      formData: {},
      showTestAreaState: false
    };
  },
  mounted() {
    this.data["offsetTop"] = this.$refs.card.offsetTop;
    this.data["clientHeight"] = this.$refs.card.clientHeight;
    // console.log(this.$refs);
  },
  methods: {
    showTestArea() {
      console.log(this.$refs.testArea.scrollHeight);
    },
    // 处理文件上传
    handleFileChange() {
      let fileDom = this.$refs.uploadFile[0];
      this.file = fileDom.files[0];
      let formData = new FormData();
      formData.append("file", this.file);
      this.formData = formData;
    },

    startTest() {
      // 处理请求参数
      let params = {};
      this.data["Params"].forEach(p => {
        params[`${p["name"]}`] = p["value"];
      });
      // 请求信息
      let requestInfo = {
        url: `http://${this.$doc_server.apiServer}/${this.data["Url"]}`,
        method: this.data["Method"],
        headers: {
          "Content-Type": this.data["Header"]
        },
        params: params
      };
      // 当没有文件传输时 不添加data属性
      if (this.file !== "") {
        requestInfo["data"] = this.formData;
      }
      // 发起请求
      this.$http(requestInfo)
        .then(res => {
          console.log(res);
          this.data["Result"] = res.data;
        })
        .catch(err => (this.data["Result"] = err));
    }
  }
};
</script>

