<template>
  <div id="app">
    <div class="app-wrapper">
      <div class="app-container">
        <!-- 侧边栏 -->
        <div class="sidebar">
          <div class="sidebar-box">
            <!-- <div class="search-box">
              <label>
                <Input label="输入搜所内容" />
              </label>
            </div> -->
            <div
              class="sidebar-area"
              v-for="(doc, index) in docs"
              :key="index"
            >
              <div
                class="menu-item"
                v-for="(item, index) in doc.Content"
                :key="index"
              >
                <!-- delete -->
                <span
                  class="mark"
                  style="background-color:#F56C6C"
                  v-if="item['Method']==='DELETE'"
                ></span>
                <!-- put -->
                <span
                  class="mark"
                  style="background-color:#E6A23C"
                  v-if="item['Method']==='PUT'"
                ></span>
                <!-- get -->
                <span
                  class="mark"
                  style="background-color:#409EFF"
                  v-if="item['Method']==='GET'"
                ></span>
                <!-- post -->
                <span
                  class="mark"
                  style="background-color:#67C23A"
                  v-if="item['Method']==='POST'"
                ></span>
                <!-- 未填写方法 -->
                <span
                  class="mark"
                  style="background-color:#303133"
                  v-if="item['Method']===''"
                ></span>
                <a
                  class="list-item"
                  :href="'#'+item['Title']"
                >{{item['Title']}}</a>
              </div>
            </div>
          </div>
        </div>

        <div
          class="index-content"
          ref="test01"
        >
          <div
            v-for="(item, index) in docs"
            :key="index"
          >
            <Index
              style="margin-top:8px"
              v-for="(con, idx) in item.Content"
              :key="idx"
              :data="con"
              ref="item"
            ></Index>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Index from "./components/Index";

export default {
  name: "app",
  components: {
    Index
  },
  data() {
    return {
      docs: [],
      sidebar: []
    };
  },
  created() {
    this.fetchTestData();
    this.fetchUpdated();
  },
  mounted() {},

  updated() {},
  methods: {
    fetchUpdated() {
      let _this = this;
      this.$ws.onmessage = function(e) {
        console.log(e);
        let result = JSON.parse(e.data);
        let name = result.Name;
        _this.docs.map(ele => {
          if (ele.Name === name) {
            ele["Content"] = _this.toDoc(result.Content);
          }
        });
      };
    },

    toDoc(contents, fn) {
      for (let i = 0; i < contents.length; i++) {
        let value = contents[i];
        if (!value["Url"]) {
          contents.splice(i, 1);
          continue;
        }
        value["Method"] = value["Method"].toUpperCase();
        value["Params"] = this.handleParams(value["Params"]);
        value["Result"] = "";
        if (!value["Header"]) {
          value["Header"] = "multipart/form-data";
        }
        fn && fn(value);
      }
      return contents;
    },

    fetchTestData() {
      this.$server.fetchDocs().then(res => {
        let docs = res.data.Docs;
        if (docs !== null && Array.isArray(docs)) {
          docs.forEach(ele => {
            let sidebarItem = [];
            ele.Content = this.toDoc(ele.Content, null);
            this.sidebar.push(sidebarItem);
          });
          this.docs = docs;
        }
        console.log(this.docs);

        // setInterval(this.fetchUpdated, 300);
      });
    },

    handleParams(params) {
      let ret = [];
      if (Array.isArray(params)) {
        for (let i = 0; i < params.length; i++) {
          let p = params[i];
          ret.push({
            name: p[0] || "",
            type: p[1] || "",
            explain: p[2] || "",
            remark: p[3] || "",
            other: p[4] || "",
            value: ""
          });
        }
      }
      return ret;
    }
  }
};
</script>

